<template>
  <div class="fullpage-container" style="position:absolute">
    <!-- <div class="button-group">
      <button type="button" :class="{active:index ==0}" @click="moveTo(0)">first page</button>
      <button type="button" :class="{active:index ==1}" @click="moveTo(1)">Second page</button>
      <button type="button" :class="{active:index ==2}" @click="moveTo(2)">Third page</button>
    </div> -->

    <!-- The dots/circles -->
    <div id="indicator" style="text-align:center">
      <span class="dot" v-bind:class="{ active: index===0}" @click="moveTo(0)"></span>
      <span class="dot" v-bind:class="{ active: false, activeYellow: index===1}" @click="moveTo(1)"></span>
      <span class="dot" v-bind:class="{ active: false, activeYellow: index===2}" @click="moveTo(2)"></span>
      <span class="dot" v-bind:class="{ active: index===3}" @click="moveTo(3)"></span>
      <span class="dot" v-bind:class="{ active: index===4}" @click="moveTo(4)"></span>
      <span class="dot" v-bind:class="{ active: false, activeYellow: index===5}" @click="moveTo(5)"></span>
    </div>
    <div class="fullpage-wp" v-fullpage="opts" ref="fullpage">



      <div class="page-1 page">


        <!-- Navbar -->
        <div class="topnav" id="myTopnav">
          <img class="logo" src="/static/devphotos/ai.png" style="width:150px;" />
          <!-- responsive menu -->
          <a href="javascript:void(0);" class="icon" id="menu" @click="openMenu()">
            <i class="fas fa-bars"></i>
          </a>

          <div id="sections">
            <a @click="moveTo(0)">Section 1</a>
            <a @click="moveTo(1)">Section 2</a>
            <a @click="moveTo(2)">Section 3</a>
            <a @click="moveTo(3)">Section 4</a>
            <a @click="moveTo(4)">Section 5</a>
            <a @click="moveTo(5)">Section 6</a>
          </div>
        </div>


        <!-- Page 1 content -->
        <h1 class="part-1" v-animate="{value: 'bounceInLeft'}">vue-fullpage.js </h1>
        <h2 v-animate="{value: 'bounceInLeft'}">scroll in pages</h2>
        <h3 class="" v-animate="{value: 'easeIn'}">A sigle-page scroll plugin based on vue@2.x,support for mobile</h3>
      </div>


      <div class="page-2 page">
        <h1 class="part-1" v-animate="{value: 'bounceInLeft'}">vue-fullpage.js </h1>
        <h2 v-animate="{value: 'bounceInLeft'}">scroll in pages</h2>
        <h3 class="" v-animate="{value: 'bounceInLeft'}">A sigle-page scroll plugin based on vue@2.x,support for mobile
          and
          PC .</h3>
      </div>

      <div class="page-3 page">
        <h1 class="part-1" v-animate="{value: 'bounceInLeft'}">vue-fullpage.js </h1>
        <h2 v-animate="{value: 'bounceInLeft'}">scroll in pages</h2>
        <h3 class="" v-animate="{value: 'bounceInLeft'}">A sigle-page scroll plugin based on vue@2.x,support for mobile
          and
          PC .</h3>

      </div>

      <div class="page-4 page">
        <h1 class="part-1" v-animate="{value: 'bounceInLeft'}">vue-fullpage.js </h1>
        <h2 v-animate="{value: 'bounceInLeft'}">scroll in pages</h2>
        <h3 class="" v-animate="{value: 'bounceInLeft'}">A sigle-page scroll plugin based on vue@2.x,support for mobile
          and
          PC .</h3>
      </div>

      <div class="page-5 page">
        <h1 class="part-1" v-animate="{value: 'bounceInLeft'}">vue-fullpage.js </h1>
        <h2 v-animate="{value: 'bounceInLeft'}">scroll in pages</h2>
        <h3 class="" v-animate="{value: 'bounceInLeft'}">A sigle-page scroll plugin based on vue@2.x,support for mobile
          and
          PC .</h3>
      </div>

      <div class="page-6 page">
        <h1 class="part-1" v-animate="{value: 'bounceInLeft'}">vue-fullpage.js </h1>
        <h2 v-animate="{value: 'bounceInLeft'}">scroll in pages</h2>
        <h3 class="" v-animate="{value: 'bounceInLeft'}">A sigle-page scroll plugin based on vue@2.x,support for mobile
          and
          PC .</h3>
      </div>



    </div>
  </div>
</template>

<script>
  export default {
    components: {},
    data() {
      var that = this;
      return {
        index: 0,
        pageNum: 0,
        opts: {
          start: 0,
          dir: 'v',
          loop: false,
          duration: 100,
          overflow: 'scroll',
          beforeChange: function (elem, current, next) {
            console.log('before', current, next)
          },
          afterChange: function (elem, current) {
            that.index = current;
            console.log('after', current)
          }
        }
      };
    },
    methods: {

      openMenu: function () {
        var x = document.getElementById("myTopnav");
        if (x.className === "topnav") {
          x.className += " responsive";
        } else {
          x.className = "topnav";
        }
      },

      closeMenu: function () {
        var x = document.getElementById("myTopnav");
        if (x.className === "topnav") {
          x.className -= " responsive";
        } else {
          x.className = "topnav";
        }
      },
      moveTo: function (index) {

        if (window.innerWidth < 768) {
          this.closeMenu()
        }
        const $fullpage = this.$refs.fullpage.$fullpage
        console.log(index);
        if (index < this.index) {
          $fullpage.pageEles[this.index].scrollTo(0, 0)
        } else {
          $fullpage.pageEles[this.index].scrollTo(0, $fullpage.pageEles[this.index].scrollHeight)
        }
        $fullpage.moveTo(index, true)
        this.index = index
      },
      showPage: function () {
        this.pageNum++;
        this.$refs.fullpage.$fullpage.$update();
      }
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .page-1 {
    padding-top: 100px;
    background: #001a59;


  }

  .page-2 {
    padding-top: 100px;
    background-color: rgb(255, 255, 112);
    /* background: #1bbc9b; */
    /* background-image: url("/static/devphotos/2.png");
    background-repeat: no-repeat;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover; */
  }

  .page-3 {
    padding-top: 50px;
    background: white;
    /* background-image: url("/static/devphotos/3.png");
    background-repeat: no-repeat;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover; */
  }

  .page-4 {
    padding-top: 50px;
    background: black;

  }

  .page-5 {
    padding-top: 50px;
    background: black;
    /* background-image: url("/static/devphotos/5.png");
    background-repeat: no-repeat;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover; */
  }

  .page-6 {
    padding-top: 100px;
    background: #6c6c6c;
    /* background-image: url("/static/devphotos/6.png");
    background-repeat: no-repeat;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover; */
  }


  .topnav {
    background-color: transparent;
    overflow: hidden;
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
  }

  /* Style the links inside the navigation bar */
  .topnav a {
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: right;
    padding: 20px 16px;
    text-decoration: none;
    font-size: 17px;
    font-weight: bold;
    cursor: pointer;
  }

  /* Change the color of links on hover */
  .topnav a:hover {
    background-color: rgba(40, 40, 40, 0.201);
    /* color: rgb(255, 255, 255); */
  }

  /* Add an active class to highlight the current page */
  .active {
    background-color: #4CAF50;
    color: white;
  }

  /* Hide the link that should open and close the topnav on small screens */
  .topnav .icon {
    display: none;
  }

  .logo {
    float: left;
    margin-top: 25px;
    margin-left: 10vw;
  }

  #sections {
    float: right;
    margin-right: 50px;
    margin-top: 3px
  }

  /* When the screen is less than 600 pixels wide, hide all links, except for the first one ("Home"). Show the link that contains should open and close the topnav (.icon) */
  @media screen and (max-width: 768px) {
    .topnav a {
      display: none;
    }

    .topnav a.icon {
      float: right;
      display: block;
      position: absolute;
      top: 0px;
      right: 10px;

    }

    .logo {
      float: left;
      margin-top: 20px;
      margin-left: 20px;
    }

    #sections {
      float: center;
      margin-right: 10px;
      margin-top: 50px;
      line-height: 10px;
    }
  }

  /* The "responsive" class is added to the topnav with JavaScript when the user clicks on the icon. This class makes the topnav look good on small screens (display the links vertically instead of horizontally) */
  @media screen and (max-width: 768px) {
    .topnav.responsive {
      position: fixed;
      z-index: 100;
      background: black;
    }

    .topnav.responsive a.icon {
      right: 0;
      top: 0;
    }

    .topnav.responsive a {
      float: none;
      display: block;
      text-align: left;
    }

    #indicator {
      display: none;
    }

  }


  /* DOTS */

  #indicator {
    position: absolute;
    top: 50vh;
    right: -70px;
    z-index: 100;
    transform: rotate(90deg);
    width: 200px;
    height: 20px;
    /* background:red */

  }

  .activeYellow {
    background-color: black !important;
  }

  .dot {
    cursor: pointer;
    height: 14px;
    width: 14px;
    margin: 0 5px;
    background-color: rgb(121, 121, 121);
    display: inline-block;
    transition: background-color 0.6s ease;
  }

  .active,
  .dot:hover {
    background-color: #ebebeb;
  }

</style>



