<template>
  <div id="app">
    <Main/>
  </div>
</template>

<script>
import Main from './components/Main'

export default {
  name: 'App',
  components: {
    Main
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  background:black
}
body {
        margin: 0;
        overflow: hidden;
        background:black;
}

    .fullpage-container {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
    }
    .page {
        display: block;
        text-align: center;
        font-size: 26px;
        color: #eee;
    }

    h3,
    p {
        font-size: 16px;
    }
    .button-group {
        position: absolute;
        top: 30px;
        left: 30px;
        z-index: 9;
    }
    .button-group button {
        display: inline-block;
        margin: 10px;
        color: #000;
        background: #fff;
        background: rgba(255, 255, 255, .5);
        -webkit-border-radius: 10px;
        border-radius: 10px;
        padding: 9px 18px;
        border: none;
        outline: none;
    }
    .button-group button.active {
        background: rgba(0, 0, 0, .5);
        color: #fff;
    }
</style>



